@import "utils/vars";

.bg__header {
  /* stylelint-disable */
  background: linear-gradient(90deg, rgba(86, 204, 242, 1) 50%, rgba(252, 252, 253, 1) 50%);
  /* stylelint-enable */
}

.bg__features {
  background: $c-sky;
}

.bg__services,
.bg__apply,
.bg__testimonials,
.bg__contact-us,
.bg__footer {
  background-color: $c-white;
}

@media (max-width: $tablet-view) {
  .bg__header {
    background: $c-main-bg;
  }
}
